// Mixin
//// ------------->
@mixin bg {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat; }
@mixin flex_center {
  display: flex;
  justify-content: center;
  align-items: center; }
@mixin float($l, $w) {
  float: $l;
  width: $w; }
@mixin size($w,$h:$w) {
  width: $w;
  height: $h; }
@mixin poa($t,$l:$t) {
  top: $t;
  left: $l;
  position: absolute;
  transform: translate(-50%,-50%); }
// Color
//// ------------->
$colorY: #b7985b;
$colorG: #333333;
// Font
//// ------------->
h1 {
  font-size: 13px;
  font-weight: 900;
  color: rgba($colorG, .7);
  position: relative;
  display: inline-block;
  span {
    color: $colorG;
    display: block;
    font-size: 26px;
    letter-spacing: 1px;
    margin-top: 10px; }
  &:before {
    @include size(25%, 1px);
    @include poa(150%, 50%);
    background: $colorG;
    content: ''; } }
h3 {
  font-weight: 900;
  font-size: 26px;
  font-weight: 900;
  margin-bottom: 40px;
  span {
    margin-top: 10px;
    font-size: 14px;
    color: #888;
    padding-left: 10px; } }
// Global
//// ------------->
.clearfix {
  clear: both; }
.font-weight900 {
  font-weight: 900; }
.line-height18 {
  line-height: 1.8; }
.margin-b20 {
  margin-bottom: 20px; }
.margin-b30 {
  margin-bottom: 30px; }
.padding-tb70 {
  padding: 70px 0; }
.padding-left30 {
  padding-left: 30px; }
.text-center {
  text-align: center; }
.wrap {
  width: 1250px;
  margin: 0 auto;
  position: relative;
  box-sizing: border-box; }
// Theme
//// ------------->
* {
  // border: solid 1px #888 !important
 }  // box-sizing: border-box
.pageWrap {
  .titleTop {}
  .pageMenu {
    nav {
      margin-bottom: 30px;
      a {
        color: $colorG;
        padding: 0 25px;
        position: relative;
        transition: .5s;
        &:before {
          @include size(60%, 2px);
          @include poa(100%, 50%);
          background: $colorY;
          content: '';
          transition: .5s;
          opacity: 0; } }
      .current {
        color: $colorY;
        &:before {
          top: 160%;
          opacity: 1; } } } } }
.aboutSection {
  padding: 50px 0;
  background: url('https://www.dahuandesign.com/images/bg02.jpg');
  .img {
    @include float(left, 40%);
    img {
      width: 100%; } }
  .text {
    @include float(left, 60%);
    box-sizing: border-box;
    padding: 0 50px; } }
.mediaSection {
  @include bg;
  background: url('https://www.dahuandesign.com/images/bg02.jpg');
  .post {
    float: left;
    width: 33%;
    margin-bottom: 50px;
    .text {
      color: #444; } } }
.qusition {
  .faqList {
    li {
      margin-bottom: 15px;
      padding: 5px;
      border-bottom: 1px solid #ccc;
      h3 {
        position: relative;
        padding: 10px 5px;
        font-weight: 900;
        font-size: 15px;
        color: #474747;
        cursor: pointer; }
      a.toggleOpen {
        position: absolute;
        right: 5px;
        top: 50%;
        display: block;
        margin-top: -13px;
        padding: 4px 8px;
        font-size: 13px;
        color: #fff;
        background-color: #b7985b;
        cursor: pointer;
        user-select: none; }
      .definition {
        display: none;
        margin-top: 10px;
        padding: 15px;
        font-size: 14px;
        color: #686868;
        line-height: 1.8;
        background-color: #e7e7e7; }
      .textEditor {
        clear: both;
        font-size: 15px;
        color: #444;
        line-height: 1.8; } } } }
